import step from '@/components/step.vue';

export default {
	data() {
		return {
			threshold: 0.3, //阈值
			data: null,
			errorImg: 'https://cdn.s.bld365.com/mangguovideoresult_errorimg.png',
			imgSrc: ''
		};
	},
	computed: {
		//是否成功
		flag() {
			return this.data && this.data.score > this.threshold && this.data.code.create == this.data.code.identify;
		}
	},
	components: {
		step
	},
	/**
	 * 周期
	 */
	onLoad(option) {
		let result;
		try {
			result = JSON.parse(option.result);
		} catch (e) {
			return;
		}

		console.log(result);
		if (result.code !== 200) {
			uni.showModal({
				title: '提示',
				content: result.systemMessage,
				showCancel: false,
				success: () => {}
			});
			return;
		}

		this.data = result.data;

		this.imgSrc = 'data:image/jpeg;base64,' + result.data.pic_list[0].pic;
	},
	onShow() {},
	onReady() {},
	onHide() {},
	onUnload() {},
	onPullDownRefresh() {},
	onReachBottom() {},
	onPageScroll() {},
	onBackPress() {},
	/**
	 * 方法
	 */
	methods: {
		chongpai() {
			uni.navigateBack();
		},
		nextStep() {
			uni.navigateTo({
				url: '/pages/individual/signature/signature'
			});
		}
	}
};
